import React, { useState } from 'react'
import { NavLink } from 'react-router-dom';

function Navbar() {

    const [click, setClick] = useState(false);

    const changeIcon = (e) => {
        setClick(!click);
        console.log(click);
    }

    return (
        <div className="container-fluid border-bottom bg-white">
            <div className="row">
                <div className="col-12 col-md-10 mx-auto">

                    <nav className="navbar navbar-expand-lg navbar-light bg-white">
                        <div className="container-fluid nav-container">
                            <NavLink className="navbar-brand" to="/">Recipe</NavLink>

                            <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                                <span onClick={changeIcon}>
                                    <i className={click ? 'fas fa-times fa-1.5x' : 'fas fa-bars fa-1.5x'}></i>
                                </span>
                            </button>

                            <div className="collapse navbar-collapse" id="navbarSupportedContent">
                                <ul className="navbar-nav">
                                    <li className="nav-item">
                                        <NavLink activeClassName="nav-active" exact
                                        className="nav-link active" aria-current="page" to="/">Home</NavLink>
                                    </li>
                                    <li className="nav-item">
                                        <NavLink activeClassName="nav-active"
                                         className="nav-link" to="/finder">Explore</NavLink>
                                    </li>
                                    <li className="nav-item">
                                        <NavLink activeClassName="nav-active"
                                        className="nav-link" to="/about">About Us</NavLink>
                                    </li>
                                    <li className="nav-item">
                                        <NavLink activeClassName="nav-active"
                                        className="nav-link" to="/service">Service</NavLink>
                                    </li>
                                    <li className="nav-item">
                                        <NavLink activeClassName="nav-active"
                                        className="nav-link" to="/contact">Contact Us</NavLink>
                                    </li>
                                    
                                </ul>
                            </div>

                        </div>
                    </nav>
                </div>
            </div> 
        </div>
    )
}

export default Navbar;
